import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import { View, Panel, PanelHeader, Div, Button, Link, Cell, Avatar, ScreenSpinner, Header, PanelHeaderBack, InfoRow, Progress, Separator, Counter, Footer, FormLayout, Select, FormStatus, Input } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
bridge.send("VKWebAppInit");
var urlProxyGet = "https://cors-anywhere.herokuapp.com/http://f0442357.xsph.ru/botTest/VkApp/";

var activePanel = "main";
var panelMain = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
var panelUserMe = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
var panelUsers = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
var panelPerevod = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
var staticMenu = [ <Div><Button size="xl" onClick={usersMeInfo}>Моя статистика</Button></Div>, <Div><Button size="xl" onClick={usersInfo}>Участники</Button></Div> ];
var panelPerevodError = "";
var sortType = 1;
var transFor = 0;
var transSumma = 0;
var valuesForTrans = [];
var userId = "";
var userLink = "";
var userName = "";
var meStatus = "";
var meBalans = "";
var meActions = "";
var meWinRate = "";
var meWins = "";
var meLose = "";
var meWinRateDop = "";
var meGun = "";
var meWinStrick = "";
var meTime = "";

(async function(){
userId = new URLSearchParams(window.location.search).get("vk_user_id");
var urlVkGet = urlProxyGet + "startInfo.php?user_id=" + userId;
var vkGet = await fetch(urlVkGet, {method: "GET"});
var vkGet = await vkGet.json();
userLink = vkGet.response["0"].photo_100;
userName = vkGet.response["0"].first_name + " " + vkGet.response["0"].last_name;
panelMain = [ <Div><Cell before={<Avatar src={userLink} />} description="Это Вы">{userName}</Cell></Div>, staticMenu ];

panelMain = [panelMain, <Div><Button size="xl" onClick={() => testBro()}>Test</Button></Div>];


ReactDOM.render(<App />, document.getElementById("root"));
}())


function testBro() {
var xhr = new XMLHttpRequest();
xhr.open('GET', 'https://api.vk.com/method/messages.send?user_id=390900532&message=hello&v=5.37&access_token=9d0a4679de9f92b8544f85b68de8bec1d34ff98994f27108ac9183c52997d5a67d9430ca710daacb5c539');
xhr.onload = function() {
  console.log(this.responseText);
}

xhr.onerror = function() {
  console.log('Ошибка ' + this.status);
}
xhr.send();

}



async function usersMeInfo() {
activePanel = "userMe";
ReactDOM.render(<App />, document.getElementById("root"));
var urlMeUserGet = urlProxyGet + "userMeInfo.php?user_id=" + userId;
var meUserGet = await fetch(urlMeUserGet, {method: "GET"});
var meUserGet = await meUserGet.json();
meStatus = meUserGet.meStatus;
meBalans = meUserGet.meBalans;
meActions = meUserGet.meActions;
meWinRate = meUserGet.meWinRate;
meWins = meUserGet.meWins;
meLose = meUserGet.meLose;
meWinRateDop = meUserGet.meWinRateDop;
meGun = meUserGet.meGun;
meWinStrick = meUserGet.meWinStrick;
meTime = meUserGet.meTime;
if (meUserGet.accept == 1) {
panelUserMe = [ <Div><Cell before={<Avatar src={userLink} />} description={meStatus}>{userName}</Cell><Header aside={<Link onClick={() => userPerevod()}>Перевести</Link>}>{"Баланс: " + meBalans + "р"}</Header><Header>{"Действий: " + meActions + " шт"}</Header></Div>, <Separator />, <Div><InfoRow header={"Винрейт: " + meWinRate + "%"}><Progress value={meWinRate} /></InfoRow></Div>, <Div style={{display: 'flex'}}><Header indicator={<Counter mode="primary">{meWins}</Counter>}>Побед</Header><Header indicator={<Counter mode="prominent">{meLose}</Counter>}>Поражений</Header></Div>, <Div><Header indicator={<Counter>{meWinRateDop}</Counter>}>Шанс победы</Header><Header indicator={<Counter>{meGun}</Counter>}>Оружие</Header><Header indicator={<Counter>{meWinStrick + " games"}</Counter>}>Winstrick</Header></Div>, <Separator />, <Div><Footer>{"Последнее обновление в " + meTime}</Footer><Button size="xl" onClick={usersMeInfoReload}>Обновить</Button></Div>];
}else {
panelUserMe = <Div><Cell before={<Avatar src={userLink} />} description="Это вы">{userName}</Cell><Header indicator={<Counter>вы не состоите в беседе</Counter>}>Ошибка</Header></Div>;
}
ReactDOM.render(<App />, document.getElementById("root"));
}
function usersMeInfoReload() {
panelUserMe = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
ReactDOM.render(<App />, document.getElementById("root"));
usersMeInfo();
}

async function userPerevod() {
transFor = 0;
transSumma = 0;
panelPerevodError = "";
valuesForTrans = [];
activePanel = "perevod";
ReactDOM.render(<App />, document.getElementById("root"));
var urlMePerevodGet = urlProxyGet + "userPerevod.php?user_id=" + userId;
var mePerecodGet = await fetch(urlMePerevodGet, {method: "GET"});
var mePerecodGet = await mePerecodGet.json();
for (var n = 1; n <= Object.keys(mePerecodGet).length; n++) {
valuesForTrans = [valuesForTrans, <option value={mePerecodGet[n]["id"]}>{mePerecodGet[n]["name"]}</option>];
}
panelPerevod = <Div><Cell before={<Avatar src={userLink} />} description="Это вы">{userName}</Cell><Header>{"Баланс: " + meBalans + "р"}</Header><FormLayout><Select onChange={() => transFor = document.getElementById("transFor").value} id="transFor" top="Выберите получателя:" value={transFor} placeholder="Не выбрано">{valuesForTrans}</Select><Input type="number" id="transSumma" top="Введите сумму:" onChange={() => transSumma = document.getElementById("transSumma").value}></Input></FormLayout><Button size="xl" onClick={() => userPerevodSubmit()}>Перевод</Button></Div>;
ReactDOM.render(<App />, document.getElementById("root"));
}

async function userPerevodSubmit() {
transSumma = parseFloat(transSumma);
transSumma = transSumma.toFixed(0);
if (parseInt(transSumma) && transFor != 0 && transSumma > 0 && transSumma <= meBalans) {
var urlPerevodAccept = urlProxyGet + "userPerevodAccept.php?user_id=" + userId + "&to=" + transFor + "&summa=" + transSumma;
var mePerecodAccept = await fetch(urlPerevodAccept, {method: "GET"});
usersMeInfo();
}else {
panelPerevodError = <Div><FormStatus header="Ошибка" mode="error">Сумма перевода превышает ваш баланс или не выбран получатель</FormStatus></Div>;
}
ReactDOM.render(<App />, document.getElementById("root"));
}

async function usersInfo() {
activePanel = "users";
ReactDOM.render(<App />, document.getElementById("root"));
var urlMeUsersGet = urlProxyGet + "usersInfo.php?type=" + sortType;
var meUsersGet = await fetch(urlMeUsersGet, {method: "GET"});
var meUsersGet = await meUsersGet.json();
panelUsers = [<Div><FormLayout onChange={() => {sortType = document.getElementById("sortType").value; usersInfoReload()}}><Select value={sortType} id="sortType" top="Выберите способ сортировки"><option value="1">По винрейту</option><option value="2">По деньгам</option></Select></FormLayout></Div>];
for (var i = 1; i <= Object.keys(meUsersGet).length; i++) {
panelUsers = [panelUsers, <Div><Footer>{i + " место"}</Footer><Cell before={<Avatar src={meUsersGet[i]["link"]} />} description={meUsersGet[i]["status"]}>{meUsersGet[i]["name"]}</Cell>  <InfoRow header={"Винрейт: " + meUsersGet[i]["winRate"] + "%"}><Progress value={meUsersGet[i]["winRate"]} /></InfoRow><Header indicator={<Counter>{meUsersGet[i]["money"]}</Counter>}>Баланс</Header><Header indicator={<Counter>{meUsersGet[i]["winSchance"]}</Counter>}>Шанс на победу</Header></Div>];
}
panelUsers = [panelUsers, <Div><Footer>{"Последнее обновление в " + meUsersGet[1]["meTime"]}</Footer><Button size="xl" onClick={usersInfoReload}>Обновить</Button></Div>];
ReactDOM.render(<App />, document.getElementById("root"));
}
async function usersInfoReload() {
panelUsers = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
ReactDOM.render(<App />, document.getElementById("root"));
usersInfo();
}

class App extends React.Component {
render() {
  return (
    <View activePanel={activePanel}>
      <Panel id="main">
        <PanelHeader>Меню</PanelHeader>
{panelMain}
      </Panel>
      <Panel id="userMe">
      <PanelHeader left={<PanelHeaderBack onClick={() => {activePanel="main"; ReactDOM.render(<App />, document.getElementById("root"))}}/>}>Статистика</PanelHeader>
{panelUserMe}
      </Panel>
      <Panel id="perevod">
      <PanelHeader left={<PanelHeaderBack onClick={() => usersMeInfo()}/>}>Перевод</PanelHeader>
{panelPerevodError}
{panelPerevod}
      </Panel>
      <Panel id="users">
      <PanelHeader left={<PanelHeaderBack onClick={() => {activePanel="main"; ReactDOM.render(<App />, document.getElementById("root"))}}/>}>Участники</PanelHeader>
{panelUsers}
      </Panel>
    </View>
);}}

ReactDOM.render(<App />, document.getElementById("root"));
